self.addEventListener("install", function (e) {
  // you and me going fishing in the dark
  const rootUrl =
    e.target.serviceWorker?.scriptURL?.replace("/js/sw.js", "") ?? "";

  e.waitUntil(
    caches.open("bcc-magic").then(function (cache) {
      return cache.addAll([
        rootUrl + "/",
        rootUrl + "/index.html",
        rootUrl + "/css/main.css",
        rootUrl + "/js/main.js",
        rootUrl + "/js/sw.js",
        rootUrl + "/svg/refresh.svg",
        rootUrl + "/svg/restart.svg",
      ]);
    })
  );
});
